import React, { useEffect, useState } from 'react';
import { useParams } from 'react-router';
import { connect } from 'react-redux';
import { changeTrack } from '../actions';
import { motion } from 'framer-motion';
import Topnav from '../component/topnav/topnav';
import TextRegularM from "../component/text/text-regular-m";
import PlayButton from '../component/buttons/play-button';
import PlaylistDetails from '../component/playlist/playlist-details';
import PlaylistFooter from '../component/playlist/playlist-footer';
import PlaylistTrack from '../component/playlist/playlist-track';
import { calcularDuracionPlaylist } from '../utils/utils';
import styles from './playlist.module.css';
import { PLAYLIST } from '../data/index';
//import { useData } from '../context/DataContext';

function PlaylistPage(props) {
    const [playlistIndex, setPlaylistIndex] = useState(undefined);
    const [isThisPlay, setIsThisPlay] = useState(false);
    const { path } = useParams();

    useEffect(() => {
        setIsThisPlay(playlistIndex === props.trackData.trackKey[0]);
    }, [playlistIndex, props.trackData.trackKey]);

    useEffect(() => {
        const fetchPlaylist = () => {
            const playlist = PLAYLIST.find(item => item.link === path);
            if (playlist) {
                changeBg(playlist.playlistBg);
                setPlaylistIndex(PLAYLIST.indexOf(playlist));
            }
        };

        fetchPlaylist();
    }, [path, PLAYLIST]);

    function changeBg(color) {
        document.documentElement.style.setProperty('--hover-home-bg', color);
    }

    const handleSongClick = async (item, song) => {
        try {
            const playlistIndex = PLAYLIST.indexOf(item);
            const songIndex = item.playlistData.findIndex(s => s.index === song.index);

            if (playlistIndex !== -1 && songIndex !== -1) {
                props.changeTrack([playlistIndex, songIndex]);
                const songAudio = await props.loadAudio(song.songUrl);
                props.playAudio(songAudio);
            } else {
                console.error('Error: Pista no encontrada');
            }
        } catch (error) {
            //console.error('Error during song playback:', error);    IMPORTANTE, CAUSADO POR EL CLICK AL CAMBIAR
        }
    };

    return (
        <motion.div
            className={styles.PlaylistPage}
            initial={{ opacity: 0, scale: 1.005 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{ duration: 1, ease: "easeInOut" }}
        >
            <div className={styles.gradientBg}></div>
            <div className={styles.gradientBgSoft}></div>
            <div className={styles.Bg}></div>

            <Topnav />

            {PLAYLIST.map((item) => {
                if (item.link === path) {
                    const duracionTotal = calcularDuracionPlaylist(item.playlistData);

                    return (
                        <div key={item.title}>
                            <PlaylistDetails 
                                data={item} 
                                duracionTotal={duracionTotal} // Pasa la duración total como prop
                            />
                        
                            <div className={styles.PlaylistIcons}>
                                <div onClick={() => handleSongClick(item, item.playlistData[0])}>
                                    <PlayButton isthisplay={isThisPlay} />
                                </div>
                            </div>

                            <div className={styles.ListHead}>
                                <TextRegularM>#</TextRegularM>
                                <TextRegularM>Título</TextRegularM>
                                <TextRegularM>Duración</TextRegularM>
                            </div>

                            <div className={styles.PlaylistSongs}>
                                {item.playlistData.map((song) => (
                                    <div 
                                        key={song.index} 
                                        onClick={() => handleSongClick(item, song)} 
                                        className={styles.SongBtn}
                                    >
                                        <PlaylistTrack 
                                            data={{
                                                listType: item.type,
                                                song: song
                                            }}
                                        />
                                    </div>
                                ))}
                            </div>

                            <PlaylistFooter data={item} />
                        </div>
                    );
                }
            })}
        </motion.div>
    );
}

const mapStateToProps = (state) => ({
    trackData: state.trackData,
});

export default connect(mapStateToProps, { changeTrack })(PlaylistPage);