import TitleS from '../text/title-s';
import TextBoldM from '../text/text-bold-m';
import TextRegularM from '../text/text-regular-m'; // Asegúrate de que este componente exista o usa otro estilo adecuado
import styles from "./playlist-details.module.css";

function PlaylistDetails({ data, duracionTotal }) {
    return (
        <div className={styles.playlistDetails}>
            <div className={styles.imgBox}>
                <img src={data.imgUrl} alt={`${data.title} cover`} />
            </div>
            <div className={styles.textBox}>
                <TitleS>{data.type}</TitleS>
                <h1>{data.title}</h1>
                <div className={styles.Artist}>
                    <figure>
                        <img src={data.artistImgUrl} alt={`${data.artist} image`} />
                    </figure>
                    {/* <TextBoldM>{data.artist}</TextBoldM> */}
                    <a className={styles.artistLink} href={data.link} rel="noopener noreferrer">
                        <TextBoldM>{data.artist}</TextBoldM>
                    </a>
                
                    <TextRegularM>⌁ {duracionTotal}</TextRegularM>
                </div>
                
            </div>
        </div>
    );
}

export default PlaylistDetails;


